import React ,{ useState,useEffect }from "react";
import Post from "../Posts/Post/Post";
import FileBase from 'react-file-base64';
import { TextField,Button,Typography,Paper  } from "@material-ui/core";
import useStyles from './styles';
import { useDispatch } from "react-redux";
import {createPost,updatePost} from '../../actions/posts';
import { useSelector } from "react-redux";
const Form=({currentId,setCurrentId})=>{
        const posts = useSelector((state) => state.posts);
        const post = currentId ? posts.find((p) => p._id === currentId) : null;
        const user= JSON.parse(localStorage.getItem('profile'));
useEffect(() => {
    if (post) {
        setPostData(post);
    }
}, [post]);
 const [postData,setPostData]=useState({
     
        title:'',
        message:'',
        tags:'',
        selectedFile:''
    });
    const  classes = useStyles();
    const dispatch=useDispatch();
    const clear=()=>{
        setPostData({  title: '', message: '', tags: '', selectedFile: '' });
        setCurrentId(null);
    }
    const handleSubmit=(e)=>{

     e.preventDefault();
    console.log(currentId);   
if(currentId){
    console.log(currentId);
    dispatch(updatePost(currentId,{...postData, name: user?.result?.name}))
    clear();
}
else {
   (user?.result?.sub)?(dispatch(createPost({...postData, name: user?.result?.name ,creator: user?.result?.sub}))):(dispatch(createPost({...postData, name: user?.result?.name ,creator: user?.result?._id})))

    clear();
}

}
if(!user?.result?.name){
return (<Paper  className={classes.paper}>
   <Typography variant="h6" align="center">Please Sign In to create your own post and like other's post</Typography>
</Paper>)

    }
   
    return (
<Paper elevation={3} className={classes.paper}>
   <form autoComplete="off" noValidate className={`${classes.form} ${classes.root}`} onSubmit={handleSubmit}>
      <Typography variant="h6">{!currentId ? 'Create a Post' : 'Update a Post'}</Typography>
     
      <TextField name="title" variant="outlined" label="Title" fullWidth value={postData.title} onChange={(e)=>setPostData({...postData, title:e.target.value})}/>
      <TextField name="message" variant="outlined" label="Message" fullWidth multiline rows={4} value={postData.message} onChange={(e)=>setPostData({...postData, message:e.target.value})}/>
      <TextField name="tags" variant="outlined" label="Tags" fullWidth value={postData.tags} onChange={(e)=>setPostData({...postData, tags:(e.target.value).split(",")})}/>
      <div className={classes.fileInput}>
        <FileBase type="file" multiple={false} onDone={({base64})=>setPostData({...postData, selectedFile:base64})}/>
      </div>

<Button className={classes.buttonSubmit} variant="contained" color="primary" size="large" type="submit" fullWidth>Submit</Button>
      <Button variant="contained" color="secondary" size="small" onClick={() => clear()} fullWidth>Clear</Button>
   </form>
</Paper>

    );
}

export default Form;