import React, { useState } from 'react';
import { Avatar, Button, Paper, Grid, Typography, Container } from '@material-ui/core';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import useStyles from './styles';
import Input from './input';
import { useDispatch } from 'react-redux';
import Icon from './icon';
import { useHistory } from 'react-router-dom';
import {signin, signup} from '../../actions/auth';
import { GoogleLogin } from '@react-oauth/google';
import  jwtDecode  from "jwt-decode";
const initialState = {
  email: '',
  password: '',
  firstName: '',
  lastName: '',
  confirmPassword: ''
};
const Auth = () => {
  const dispatch = useDispatch();
  const classes = useStyles();
  const [showPassword, setShowPassword] = useState(false);
  const [isSignUp, setIsSignUp] = useState(false);
  const state = null;
  const [formData, setFormData] = useState(initialState);
  const history = useHistory();
  const handleShowPassword = () =>
    setShowPassword((prevShowPassword) => !prevShowPassword);

  const handleSubmit = (e) => {
    console.log(formData)
 if(isSignUp){
   e.preventDefault();
   dispatch(signup(formData, history));
 }
 else{
     e.preventDefault();

   dispatch(signin(formData, history));
 }
  };
  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const switchMode = () => setIsSignUp((prevIsSignUp) => !prevIsSignUp);
  
  
  const googleSuccess = async (res) => {
    console.log(res);
    const decoded =  jwtDecode(res?.credential);
 try {
                // Dispatch login action with user data
                dispatch({ type: 'AUTH', data: { result: decoded, token: res?.credential } });

                history.push('/');

              } catch (err) {
                console.error('Error decoding Google credential', err);
              }
  }

  const googleError = () => alert('Google Sign In was unsuccessful. Try again later');

  return (
    <Container component="main" maxWidth="xs">
      <Paper className={classes.paper} elevation={3}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography variant="h5">
          {isSignUp ? 'Sign Up' : 'Sign In'}
        </Typography>
        <form className={classes.form} onSubmit={handleSubmit}>
          <Grid container spacing={2}>
            {isSignUp && (
              <>
                <Input
                  name="firstName"
                  label="First Name"
                  handleChange={handleChange}
                  autoFocus
                  half
                />
                <Input
                  name="lastName"
                  label="Last Name"
                  handleChange={handleChange}
                  half
                />
              </>
            )}
            <Input
              name="email"
              label="Email Address"
              handleChange={handleChange}
              type="email"
            />
            <Input
              name="password"
              label="Password"
              handleChange={handleChange}
              type={showPassword ? 'text' : 'password'}
              handleShowPassword={handleShowPassword}
            />
            {isSignUp && (
              <Input
                name="confirmPassword"
                label="Repeat Password"
                handleChange={handleChange}
                type="password"
              />
            )}
          </Grid>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            {isSignUp ? 'Sign Up' : 'Sign In'}
          </Button>

          {/* ✅ Fixed Google Sign In, keeping styling */}
          <GoogleLogin
            onSuccess={googleSuccess}
            onError={googleError}
             // optional: enables Google One Tap
            render={(renderProps) => (
              <Button className={classes.googleButton} color="primary" fullWidth onClick={renderProps.onClick} disabled={renderProps.disabled}  variant="contained">
                Google Sign In
              </Button>
            )}
          />

          <Grid container justifyContent="flex-end">
            <Grid item>
              <Button onClick={switchMode}>
                {isSignUp
                  ? 'Already have an account? Sign In'
                  : "Don't have an account? Sign Up"}
              </Button>
            </Grid>
          </Grid>
        </form>
      </Paper>
    </Container>
  );
};

export default Auth;